import React from 'react';

export function mergeRef<T>(
  ...refs: Array<React.Ref<T>>
): React.Ref<T> {

  return (node: T) => {
    for (const ref of refs) {
      if (typeof ref === 'function') {
        ref(node);
      } else if (ref && 'current' in ref) {
        (ref as any).current = node;
      }
    }
  }
}
